'use client';

import { useState } from 'react';
import { analyzePatient } from './services/api';

export default function SafeDischarge() {
  const [step, setStep] = useState<'form' | 'results'>('form');
  const [loading, setLoading] = useState(false);
  const [analysis, setAnalysis] = useState<any>(null);
  const [discharge, setDischarge] = useState<any>(null);
  
  const [form, setForm] = useState({
    age: '48',
    gender: 'Male',
    hr: '105',
    bp: '118/76',
    temp: '38.2',
    rr: '22',
    o2: '96',
    history: 'Atrial Fibrillation, Hypertension',
    meds: [{ name: 'Warfarin', dose: '5mg', freq: 'daily' }],
    labs: [{ name: 'INR', value: '3.8', ref: '2.0-3.0' }],
    complaint: 'Shortness of breath, calf pain',
    notes: 'Patient reports recent calf pain and shortness of breath'
  });

  const analyze = async () => {
    setLoading(true);
    try {
      const data = {
        demographics: { age: parseInt(form.age), gender: form.gender },
        vital_signs: {
          heart_rate: parseInt(form.hr),
          blood_pressure: form.bp,
          temperature: parseFloat(form.temp),
          respiratory_rate: parseInt(form.rr),
          oxygen_saturation: parseInt(form.o2)
        },
        medical_history: form.history.split(',').map(h => h.trim()),
        medications: form.meds.filter(m => m.name),
        lab_results: form.labs.filter(l => l.name).map(l => ({
          name: l.name,
          value: l.value,
          reference_range: l.ref
        })),
        chief_complaint: form.complaint,
        clinical_notes: form.notes
      };
      
      const result = await analyzePatient(data);
      setAnalysis(result.analysis);
      setDischarge(result.discharge);
      setStep('results');
    } catch (err) {
      console.error(err);
      alert('Analysis failed!');
    }
    setLoading(false);
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-900 via-blue-900 to-slate-900">
      <header className="bg-slate-900/50 backdrop-blur border-b border-slate-700/50">
        <div className="max-w-7xl mx-auto px-6 py-4 flex justify-between items-center">
          <div className="flex items-center gap-3">
            <div className="w-10 h-10 bg-gradient-to-br from-blue-500 to-cyan-400 rounded-lg flex items-center justify-center">
              <svg className="w-6 h-6 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12l2 2 4-4m5.618-4.016A11.955 11.955 0 0112 2.944a11.955 11.955 0 01-8.618 3.04A12.02 12.02 0 003 9c0 5.591 3.824 10.29 9 11.622 5.176-1.332 9-6.03 9-11.622 0-1.042-.133-2.052-.382-3.016z" />
              </svg>
            </div>
            <div>
              <h1 className="text-xl font-bold text-white">SafeDischarge AI</h1>
              <p className="text-sm text-blue-300">Clinical Safety Copilot</p>
            </div>
          </div>
          <div className="px-3 py-1.5 bg-green-500/20 border border-green-500/30 rounded-full flex items-center gap-2">
            <div className="w-2 h-2 bg-green-400 rounded-full animate-pulse"></div>
            <span className="text-sm text-green-300">Ready</span>
          </div>
        </div>
      </header>

      <main className="max-w-7xl mx-auto px-6 py-8">
        {step === 'form' ? (
          <div className="max-w-4xl mx-auto">
            <h2 className="text-3xl font-bold text-white mb-6">Patient Assessment</h2>
            <div className="bg-slate-800/50 rounded-xl border border-slate-700/50 p-6 space-y-6">
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm text-slate-300 mb-2">Age</label>
                  <input value={form.age} onChange={e => setForm({...form, age: e.target.value})} className="w-full px-4 py-2 bg-slate-900/50 border border-slate-600 rounded-lg text-white" />
                </div>
                <div>
                  <label className="block text-sm text-slate-300 mb-2">Gender</label>
                  <select value={form.gender} onChange={e => setForm({...form, gender: e.target.value})} className="w-full px-4 py-2 bg-slate-900/50 border border-slate-600 rounded-lg text-white">
                    <option>Male</option>
                    <option>Female</option>
                  </select>
                </div>
              </div>

              <div>
                <h3 className="text-lg font-semibold text-white mb-4">Vital Signs</h3>
                <div className="grid grid-cols-2 gap-4">
                  <input value={form.hr} onChange={e => setForm({...form, hr: e.target.value})} placeholder="Heart Rate" className="px-4 py-2 bg-slate-900/50 border border-slate-600 rounded-lg text-white" />
                  <input value={form.bp} onChange={e => setForm({...form, bp: e.target.value})} placeholder="BP" className="px-4 py-2 bg-slate-900/50 border border-slate-600 rounded-lg text-white" />
                  <input value={form.temp} onChange={e => setForm({...form, temp: e.target.value})} placeholder="Temp" className="px-4 py-2 bg-slate-900/50 border border-slate-600 rounded-lg text-white" />
                  <input value={form.rr} onChange={e => setForm({...form, rr: e.target.value})} placeholder="RR" className="px-4 py-2 bg-slate-900/50 border border-slate-600 rounded-lg text-white" />
                  <input value={form.o2} onChange={e => setForm({...form, o2: e.target.value})} placeholder="O2 Sat" className="px-4 py-2 bg-slate-900/50 border border-slate-600 rounded-lg text-white" />
                </div>
              </div>

              <div>
                <label className="block text-sm text-slate-300 mb-2">Medical History</label>
                <textarea value={form.history} onChange={e => setForm({...form, history: e.target.value})} className="w-full px-4 py-2 bg-slate-900/50 border border-slate-600 rounded-lg text-white h-24" />
              </div>

              <div>
                <label className="block text-sm text-slate-300 mb-2">Chief Complaint</label>
                <input value={form.complaint} onChange={e => setForm({...form, complaint: e.target.value})} className="w-full px-4 py-2 bg-slate-900/50 border border-slate-600 rounded-lg text-white" />
              </div>

              <div>
                <label className="block text-sm text-slate-300 mb-2">Clinical Notes</label>
                <textarea value={form.notes} onChange={e => setForm({...form, notes: e.target.value})} className="w-full px-4 py-2 bg-slate-900/50 border border-slate-600 rounded-lg text-white h-32" />
              </div>

              <button onClick={analyze} disabled={loading} className="w-full px-6 py-4 bg-gradient-to-r from-blue-600 to-cyan-500 hover:from-blue-700 hover:to-cyan-600 text-white font-semibold rounded-lg transition-all flex items-center justify-center gap-3 disabled:opacity-50">
                {loading ? 'Analyzing...' : 'Analyze Patient'}
              </button>
            </div>
          </div>
        ) : (
          <div>
            <div className="mb-6 flex justify-between items-center">
              <h2 className="text-3xl font-bold text-white">Analysis Results</h2>
              <button onClick={() => setStep('form')} className="px-4 py-2 bg-slate-700 hover:bg-slate-600 text-white rounded-lg">New Assessment</button>
            </div>

            <div className="grid grid-cols-2 gap-6">
              <div className="bg-slate-800/50 rounded-xl border border-slate-700/50 p-6">
                <h3 className="text-xl font-bold text-white mb-4">Clinical Analysis</h3>
                {analysis && (
                  <div className="space-y-4">
                    <div className="bg-red-500/20 border border-red-500/30 rounded-lg p-4">
                      <p className="text-red-300 font-semibold">{analysis.risk_stratification.overall_risk} Risk</p>
                    </div>
                    <p className="text-slate-300 text-sm">{analysis.summary}</p>
                    {analysis.flags.map((f: any, i: number) => (
                      <div key={i} className="border-l-4 border-red-500 bg-red-500/10 p-3 rounded">
                        <p className="font-semibold text-white text-sm">{f.title}</p>
                        <p className="text-slate-300 text-xs mt-1">{f.description}</p>
                      </div>
                    ))}
                  </div>
                )}
              </div>

              <div className="bg-slate-800/50 rounded-xl border border-slate-700/50 p-6">
                <h3 className="text-xl font-bold text-white mb-4">Patient Discharge Instructions</h3>
                {discharge && (
                  <div className="space-y-4">
                    <div className="bg-green-900/20 border border-green-500/30 rounded-lg p-4">
                      <p className="text-slate-200 text-sm">{discharge.patient_facing}</p>
                    </div>
                    <div className="bg-red-900/20 border border-red-500/30 rounded-lg p-4">
                      <h4 className="text-sm font-semibold text-red-300 mb-2">Emergency Care</h4>
                      <p className="text-slate-200 text-sm">{discharge.safety_netting}</p>
                    </div>
                  </div>
                )}
              </div>
            </div>
          </div>
        )}
      </main>
    </div>
  );
}
